import React, { useState, useEffect } from 'react';
import '../WeatherDashboard.css';
import { Line } from 'react-chartjs-2';
import Chart from 'chart.js/auto'; // Asegúrate de importar 'chart.js/auto'
import 'chartjs-adapter-moment'; // Si es necesario para tu versión de Chart.js
import moment from 'moment'; // Importa moment.js

function WeatherDashboard() {
  const [weatherData, setWeatherData] = useState(null);

  useEffect(() => {
    // Realiza la solicitud HTTP a la API utilizando fetch
    fetch('https://api.open-meteo.com/v1/forecast?latitude=-31.4135&longitude=-64.181&hourly=temperature_2m,relativehumidity_2m')
      .then((response) => {
        if (!response.ok) {
          throw new Error('La solicitud no fue exitosa');
        }
        return response.json();
      })
      .then((data) => {
        // Al recibir los datos, actualiza el estado
        console.log('Datos recibidos:', data);
        setWeatherData(data);
      })
      .catch((error) => {
        console.error('Error al obtener datos de la API:', error);
      });
  }, []);

  return (
    <div className="weather-dashboard">
      <div className="left-panel">
        {/* Contenido del panel izquierdo */}
        <h2>Panel Izquierdo</h2>
        {/* Agrega contenido según tus necesidades */}
      </div>
      <div className="right-panel">
        {weatherData && weatherData.hourly && (
          <>
            <div className="top-right">
              {/* Contenido del panel superior del lado derecho */}
              <h2>Panel Superior</h2>
              <TemperatureChart
                timeLabels={weatherData.hourly.time}
                temperatureData={weatherData.hourly.temperature_2m}
              />
            </div>
            <div className="bottom-right">
              {/* Contenido del panel inferior del lado derecho */}
              <h2>Panel Inferior</h2>
              <HumidityChart data={weatherData.hourly.relativehumidity_2m} />
            </div>
          </>
        )}
      </div>
    </div>
  );
}

function TemperatureChart({ timeLabels, temperatureData }) {
  console.log('timeLabels', timeLabels)
  console.log('temperatureData', temperatureData)
  // Convierte los datos de tiempo a un formato compatible con Moment.js
  const formattedTimeLabels = timeLabels.map((time) => moment(time));

  const chartData = {
    labels: formattedTimeLabels, // Usamos las etiquetas de tiempo como etiquetas en el eje X
    datasets: [
      {
        label: 'Temperatura (°C)',
        data: temperatureData, // Datos de temperatura en el eje Y
        fill: false,
        borderColor: 'blue',
        borderWidth: 2,
      },
    ],
  };


  const chartOptions = {
    scales: {
      x: {
        type: 'time',
        time: {
          unit: 'hour', // Personaliza la unidad de tiempo según tus necesidades
        },
        title: {
          display: true,
          text: 'Hora',
        },
      },
      y: {
        title: {
          display: true,
          text: 'Temperatura (°C)',
        },
      },
    },
  };

  return (
    <div className="temperature-chart temperature-chart-small">
      <h3>Gráfico de Temperatura</h3>
      <Line data={chartData} options={chartOptions} />
    </div>
  );
}

function HumidityChart({ data }) {
  if (!data) {
    return null; // Evita errores si los datos no están disponibles
  }

  return (
    <div className="humidity-chart">
      <h3>Gráfico de Humedad Relativa</h3>
      <div className="chart">
        {data.map((humidity, index) => (
          <div
            className="bar"
            key={index}
            style={{ height: `${humidity}%` }}
          >
            {humidity}%
          </div>
        ))}
      </div>
    </div>
  );
}

export default WeatherDashboard;
